<template>
  <md-dialog ref="create_new_file">
    <md-dialog-title>Create new file</md-dialog-title>
    <md-dialog-content>
      <md-input-container>
        <label>File name</label>
        <md-input :value="filename" id="create-filename-input"></md-input>
      </md-input-container>
    </md-dialog-content>
    <md-dialog-actions>
      <md-button class="md-primary" @click.native="createNew()" id='create-new-file-button'>Create</md-button>
      <md-button @click.native="openFromGDrive()" id='open-from-gdrive-button'>open from Google Drive <img v-bind:src="gdriveSvg" /></md-button>
    </md-dialog-actions>
  </md-dialog>
</template>

<script>
import gdriveSvg from '@/assets/google-drive.svg'
import { file } from '@/services'

export default {
  data () {
    return {
      filename: 'New document',
      gdriveSvg
    }
  },
  methods: {
    openDialog () {
      this.filename = 'New document'
      this.$refs.create_new_file.open()
    },
    createNew () {
      this.$refs.create_new_file.close()
      file.createNewFile(this.$el.querySelector('#create-filename-input').value)
    },
    openFromGDrive () {
      file.openFromGDrive()
        .then(() => { this.$refs.create_new_file.close() })
        .catch(() => { console.info('Canceled gapi picker.') })
    },
    updateFilename ($event) {
      this.filename = $event.value
    }
  }
}
</script>



